<template>
  <div class="home">
    <van-sticky>
      <van-nav-bar title="地址管理" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    <!-- 列表 -->
    <div class="address-list" v-if="listData.length > 0">
      <div class="address-item" v-for="(item, index) in listData" :key="index">
        <div class="address-item-content" @click="selectAddress(item)">
          <div>
            <span class="address-item-content-name">{{ item.name }}</span>
            <span>{{ item.phone }}</span>
          </div>
          <div class="address-item-content-address van-ellipsis">
            {{ item.province }}{{ item.city }}{{ item.area }}{{ item.detail }}
          </div>
        </div>
        <div class="address-item-ctr">
          <div
            class="address-item-ctr-left"
            @click="defaultAddress(item.id, item.status)"
          >
            <van-icon name="circle" v-if="item.status == 0"></van-icon>
            <van-icon color="#3399FF" name="checked" v-else></van-icon>
            <span>默认地址</span>
          </div>
          <div class="address-item-ctr-right">
            <div class="address-item-edit" @click="editAddress(item)">
              <van-icon name="edit"></van-icon>
              <span>编辑</span>
            </div>
            <div class="address-item-delete" @click="addressDelete(item.id)">
              <van-icon name="delete"></van-icon>
              <span>删除</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-else class="no-data">暂无数据!</div>
    <div class="addBtn" @click="toAdd">
      添加收货地址
    </div>
  </div>
</template>

<script>
import { addressList, deleteAddress, setDefault } from "@/api/mall";
import { Dialog } from "vant";
export default {
  data() {
    return {
      listData: []
    };
  },
  created() {
    this.getList();
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    getList() {
      addressList().then(res => {
        this.listData = res;
      });
    },
    toAdd() {
      this.$router.push({ path: "editAddress" });
    },
    editAddress(item) {
      this.$router.push({
        path: "editAddress",
        query: { id: item.id, type: 2 }
      });
    },
    addressDelete(id) {
      Dialog.confirm({
        title: "删除地址",
        message: "是否删除该地址？"
      })
        .then(() => {
          this.$toast.loading("处理中...");
          deleteAddress(id).then(resp => {
            this.$toast.success("删除成功");
            this.getList();
          });
        })
        .catch(() => {
          this.$toast("取消操作");
        });
    },
    // 设置默认地址
    defaultAddress(id, status) {
      // if (status == 0) {
        let msg=status==0?'是否设置此地址为默认地址？':'是否取消此地址为默认地址？'
        Dialog.confirm({
          title: "设置默认地址",
          message: msg
        })
          .then(() => {
            this.$toast.loading("处理中...");
            setDefault(id).then( resp => {
              this.$toast.success("设置成功");
              this.getList();
            });
          })
          .catch(() => {
            this.$toast("取消操作");
          });
      // }
    }
  }
};
</script>
<style lang="less" scoped>
.home {
  background: #f9f9f9;
  height: 100vh;
  width: 100%;
}
.address-list {
  margin-bottom: 60px;
  .address-item {
    background: #fff;
    width: 100%;
    margin: 10px auto;
    box-sizing: border-box;
    padding: 10px 23px 12px 16px;
    .address-item-content {
      font-size: 13px;
      color: #999999;
      line-height: 25px;

      .address-item-content-name {
        margin-right: 20px;
      }

      .address-item-content-address {
        height: 25px;
      }
    }

    .address-item-ctr {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 30px;
      font-size: 13px;

      .address-item-ctr-left {
        display: flex;
        align-items: center;

        .van-icon {
          margin-right: 10px;
          font-size: 16px;
        }
      }

      .address-item-ctr-right {
        width: 120px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .van-icon {
          margin-right: 8px;
        }
      }
    }
  }

  .no-data {
    line-height: 60px;
    height: 60px;
    text-align: center;
    font-size: 16px;
    color: #a8a8a8;
  }
}

.no-data {
  line-height: 34px;
  height: 34px;
  text-align: center;
  font-size: 14px;
}
.addBtn {
  width: 100%;
  height: 50px;
  text-align: center;
  line-height: 50px;
  background: #28A6FF;
  position: absolute;
  left: 0;
  color: white;
  bottom: 0;
  font-size: 20px;
}
/deep/ .van-icon-checked {
  color: #de4f53!important;
}
</style>
